import React, { Component } from 'react';
import ItemContentListMenu from './ItemContentListMenu';

export default class ContentListMenu extends Component {
    render() {
        return (
            <div className="swiper-slide">
                <div>
                    <div>
                    <div className="content_listmenu">
                    <article>{this.props.title}</article>
                    {
                        this.props.dataTheme.map((value, key) => {
                            return (
                                <ItemContentListMenu key={key}
                                    itemId={value.id}
                                    title={value.title}
                                    view={value.view}
                                    quote={value.quote}
                                    theme={value.theme}
                                />
                            );
                        })
                    }
                    </div>
                    </div>
                </div>
            </div>
        )
    }
}
