import React, { Component } from 'react';

export default class ItemContentListMenu extends Component {
    chuyenDoiURL = (str) => {
        // Chuyển hết sang chữ thường
        str = str.toLowerCase();     
 
        // xóa dấu
        str = str.replace(/(à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ)/g, 'a');
        str = str.replace(/(è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ)/g, 'e');
        str = str.replace(/(ì|í|ị|ỉ|ĩ)/g, 'i');
        str = str.replace(/(ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ)/g, 'o');
        str = str.replace(/(ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ)/g, 'u');
        str = str.replace(/(ỳ|ý|ỵ|ỷ|ỹ)/g, 'y');
        str = str.replace(/(đ)/g, 'd');
     
        // Xóa ký tự đặc biệt
        str = str.replace(/([^0-9a-z-\s])/g, '');
     
        // Xóa khoảng trắng thay bằng ký tự -
        str = str.replace(/(\s+)/g, '-');
     
        // xóa phần dự - ở đầu
        str = str.replace(/^-+/g, '');
     
        // xóa phần dư - ở cuối
        str = str.replace(/-+$/g, '');
     
        // return
        return str;
    }

    render() {
        return (
            // £40.00
            <a href={"/list-menu/" + this.chuyenDoiURL(this.props.theme)}>
                <div className="item_content_listmenu">
                    <div className="once_item_content_listmenu">
                        <div className="item_content_listmenu_title">{this.props.title}</div>
                        <div className="item_content_listmenu_space">.</div>
                        <div className="item_content_listmenu_price">
                            <svg id="Layer_eye" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 57.6 28.8">
                                <g>
                                    <path className="cls-1" d="M29,28.8h-.2v0h-.1C12.1,28.8,1,16.6.6,16.1L0,15.4l.6-.6A42.7,42.7,0,0,1,15.1,3.2,35,35,0,0,1,27.6,0H30A35,35,0,0,1,42.6,3.2,42.7,42.7,0,0,1,57.1,14.8l.5.6-.5.7c-.5.5-11.6,12.7-28.1,12.7Zm-.2-2H29v0c13.5,0,23.5-9,26-11.4A39.4,39.4,0,0,0,41.7,5,31.4,31.4,0,0,0,29,2h-.4A32.2,32.2,0,0,0,15.9,5,39.1,39.1,0,0,0,2.7,15.4c2.4,2.4,12.4,11.4,25.9,11.4Z" />
                                    <path className="cls-1" d="M28.8,28.5a14.1,14.1,0,1,1,14-14.1A14.1,14.1,0,0,1,28.8,28.5Zm0-26.2A12.1,12.1,0,1,0,40.9,14.4,12.2,12.2,0,0,0,28.8,2.3Z" />
                                    <path className="cls-1" d="M34.5,14.4a5.8,5.8,0,0,1-11.5,0,5.8,5.8,0,0,1,11.5,0Z" />
                                </g>
                            </svg>
                            <section>{this.props.view}</section>
                        </div>
                    </div>
                    <section>{this.props.quote}</section>
                </div>
            </a>
        )
    }
}
