import React, { Component } from 'react';
import SlideProductNote from './SlideProductNote';
import SlideProductImg from './SlideProductImg';
import data from './data.json'

export default class Product extends Component {
  chuyenDoiURL = (str) => {
    // Chuyển hết sang chữ thường
      str = str.toLowerCase();     
   
      // xóa dấu
      str = str.replace(/(à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ)/g, 'a');
      str = str.replace(/(è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ)/g, 'e');
      str = str.replace(/(ì|í|ị|ỉ|ĩ)/g, 'i');
      str = str.replace(/(ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ)/g, 'o');
      str = str.replace(/(ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ)/g, 'u');
      str = str.replace(/(ỳ|ý|ỵ|ỷ|ỹ)/g, 'y');
      str = str.replace(/(đ)/g, 'd');
   
      // Xóa ký tự đặc biệt
      str = str.replace(/([^0-9a-z-\s])/g, '');
   
      // Xóa khoảng trắng thay bằng ký tự -
      str = str.replace(/(\s+)/g, '-');
   
      // xóa phần dự - ở đầu
      str = str.replace(/^-+/g, '');
   
      // xóa phần dư - ở cuối
      str = str.replace(/-+$/g, '');
   
      // return
      return str;
  }
  render() {
    var dataProduct = [];
    data.map((value, key) => {
      if(this.chuyenDoiURL(value.theme) === this.chuyenDoiURL(this.props.match.params.slug)){
          var item1 = {};
              item1.id = value.id;
              item1.view = value.view;
              item1.theme = value.theme;
              item1.title = value.title;
              item1.quote = value.quote;
              item1.content = value.content;
              item1.image = value.image;
        dataProduct.push(item1);
        return dataProduct;
      }else {return true;}
    });
        return (
            <div className="product get_data_menu" data-get-menu="product">
              <div className="row d-flex">
                <div className="col-12 col-lg-6 order-md-1 slide_img_product">
                  <div className="img_product">
                    <div className="this_img_product">
                      <ul>
                        {
                          dataProduct.map((value, key) => {
                            return(
                              <SlideProductImg key={key}
                                image={value.image}
                              />
                            );
                          })
                        }
                      </ul>
                      <section />
                    </div>
                    <div className="button_slide_product">
                      <section>
                        <svg id="Layer_slide_product_prev" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 46.2 129.7">
                          <defs>
                            <style dangerouslySetInnerHTML={{__html: "\n\t\t\t\t\t\t\t\t\t\t\t#Layer_slide_product_prev .cls-1 {\n\t\t\t\t\t\t\t\t\t\t\t\tfill: none;\n\t\t\t\t\t\t\t\t\t\t\t\tstroke: #fff;\n\t\t\t\t\t\t\t\t\t\t\t\tstroke-miterlimit: 10;\n\t\t\t\t\t\t\t\t\t\t\t\tstroke-width: 2px;\n\t\t\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t\t\t" }} />
                          </defs>
                          <title>Artboard 1</title>
                          <polyline className="cls-1" points="44.1 1.4 3 64.2 44.1 128.4" />
                        </svg>
                      </section>
                      <section>
                        <svg id="Layer_slide_product_next" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 46.3 129.9">
                          <defs>
                            <style dangerouslySetInnerHTML={{__html: "\n\t\t\t\t\t\t\t\t\t\t\t#Layer_slide_product_next .cls-1 {\n\t\t\t\t\t\t\t\t\t\t\t\tfill: none;\n\t\t\t\t\t\t\t\t\t\t\t\tstroke: #fff;\n\t\t\t\t\t\t\t\t\t\t\t\tstroke-miterlimit: 10;\n\t\t\t\t\t\t\t\t\t\t\t\tstroke-width: 2px;\n\t\t\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t\t\t" }} />
                          </defs>
                          <title>Artboard 1</title>
                          <polyline className="cls-1" points="2.1 1.4 43.3 64.3 2.1 128.6" />
                        </svg>
                      </section>
                    </div>
                  </div>
                </div>
                <div className="col-12 col-lg-6 order-md-0 slide_note_product">
                <h2>{dataProduct[0].theme}</h2>
                  <div className="note_product">
                    <ul>
                      {
                        dataProduct.map((value, key) => {
                          return(
                            <SlideProductNote key={key}
                              id={value.id}
                              title={value.title}
                              content={value.content}
                              link={this.chuyenDoiURL(value.theme)}
                            />
                          );
                        })
                      }
                    </ul>
                  </div>
                </div>
              </div>
        </div>

        )
    }
}
