import React, { Component } from 'react'

export default class Contact extends Component {
    render() {
        return (
            <div className="contact get_data_menu" data-get-menu="contactus">
  <div className="row">
    <div className="col-0 col-lg-3 button_back_contact">
      <a href="/home">
        <div>
          <svg id="Layer_back_about" data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 109.2 232.9">
            <defs>
              <style dangerouslySetInnerHTML={{__html: "\n\t\t\t\t\t\t\t\t\t\t#Layer_back_about .cls-1 {\n\t\t\t\t\t\t\t\t\t\t\tfill: none;\n\t\t\t\t\t\t\t\t\t\t\tstroke: #fff;\n\t\t\t\t\t\t\t\t\t\t\tstroke-miterlimit: 10;\n\t\t\t\t\t\t\t\t\t\t\tstroke-width: 5px;\n\t\t\t\t\t\t\t\t\t\t}\n\t\t\t\t\t\t\t\t\t" }} />
            </defs>
            <title>close contact</title>
            <polyline className="cls-1" points="1.9 1.7 105.9 116.4 1.9 231.2" />
          </svg>
        </div>
      </a>
    </div>
    <div className="col-12 col-lg-9 contact_form">
      <article>
        Contact Us
      </article>
      <div>
        <form>
          <label>
            <section>Name</section>
            <input type="text" placeholder="your name" />
          </label>
          <label>
            <section>Company</section>
            <input type="text" placeholder="your company" />
          </label>
          <label>
            <section>Telephone</section>
            <input type="text" placeholder="your phone number" />
          </label>
          <label>
            <section>E-mail</section>
            <input type="text" placeholder="your email" />
          </label>
          <label>
            <section>Message</section>
            <textarea type="text" placeholder="your message" defaultValue={""} />
          </label>
          <label>
            <button type="submit">discuss</button>
          </label>
        </form>
      </div>
    </div>
  </div>
</div>

        )
    }
}
