import React, { Component } from 'react';

export default class About extends Component {
    render() {
        return (
            <div className="about get_data_menu" data-get-menu="aboutus">
              <div className="hr_center_about" />
              <div className="row d-flex">
                <div className="col-12 col-lg-6 order-md-1 content_about">
                <section>
                  <article className="title_about">
                    About
                  </article>
                  <div>
                    <p className="content_about_node">
                      CineShader is a real-time 3D shader visualiser. It leverages the
                      Shadertoy.com API to bring thousands of existing shader artworks
                      into a cinematic 3D environment.
                    </p>
                    <p className="content_about_node">
                      The whole project was started as an idea of using a web demo to
                      explain what procedural noise is to our clients at Lusion. After sending
                      out the demo to some of our friends, we were encouraged to add the
                      live editor support and we decided to release it to the public.
                    </p>
                    <p className="content_about_node">
                      We are not trying to make another Shadertoy but instead hoping to
                      give the Shadertoy users an extension to demonstrate their shaders
                      with a different presentation. Hence, all shaders are still hosted at
                      Shadertoy.com and reverse compatiable in Shadertoy.
                    </p>
                  </div>
                </section>
                <div />
                <section>
                  <article className="title_about">
                    About us
                  </article>
                  <div className="content_about_node">
                    Lusion is an award winning multidisciplinary production studio. From
                    creative to production, we collaborate with creative agencies and
                    design studios to deliver compelling, real-time experiences, which
                    go far beyond expectations.
                  </div>
                </section>
              </div>
                <div className="col-12 col-lg-6 order-md-0">
                <article className="title_about">
                  Collaborations
                </article>
                <div className="collaborations">
                  <div className="item_collaborations">
                    <img src="https://raw.githubusercontent.com/SunHarry198/images/master/logo/logo-7.png" alt="" />
                  </div>
                  <div className="item_collaborations">
                    <img src="https://raw.githubusercontent.com/SunHarry198/images/master/logo/logo-2.png" alt="" />
                  </div>
                  <div className="item_collaborations">
                    <img src="https://raw.githubusercontent.com/SunHarry198/images/master/logo/logo-3.png" alt="" />
                  </div>
                  <div className="item_collaborations">
                    <img src="https://raw.githubusercontent.com/SunHarry198/images/master/logo/logo-4.png" alt="" />
                  </div>
                  <div className="item_collaborations">
                    <img src="https://raw.githubusercontent.com/SunHarry198/images/master/logo/logo-5.png" alt="" />
                  </div>
                  <div className="item_collaborations">
                    <img src="https://raw.githubusercontent.com/SunHarry198/images/master/logo/logo-6.png" alt="" />
                  </div>
                  <div className="item_collaborations">
                    <img src="https://raw.githubusercontent.com/SunHarry198/images/master/logo/logo-2.png" alt="" />
                  </div>
                  <div className="item_collaborations">
                    <img src="https://raw.githubusercontent.com/SunHarry198/images/master/logo/logo-7.png" alt="" />
                  </div>
                  <div className="item_collaborations">
                    <img src="https://raw.githubusercontent.com/SunHarry198/images/master/logo/logo-8.png" alt="" />
                  </div>
                </div>
              </div>
              </div>
            </div>

        )
    }
}
