import React, { Component } from 'react';
import ContentListMenu from './ContentListMenu';
import data from './data.json';

export default class ListMenu extends Component {
  
  chuyenDoiURL = (str) => {
    // Chuyển hết sang chữ thường
      str = str.toLowerCase();     
   
      // xóa dấu
      str = str.replace(/(à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ)/g, 'a');
      str = str.replace(/(è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ)/g, 'e');
      str = str.replace(/(ì|í|ị|ỉ|ĩ)/g, 'i');
      str = str.replace(/(ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ)/g, 'o');
      str = str.replace(/(ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ)/g, 'u');
      str = str.replace(/(ỳ|ý|ỵ|ỷ|ỹ)/g, 'y');
      str = str.replace(/(đ)/g, 'd');
   
      // Xóa ký tự đặc biệt
      str = str.replace(/([^0-9a-z-\s])/g, '');
   
      // Xóa khoảng trắng thay bằng ký tự -
      str = str.replace(/(\s+)/g, '-');
   
      // xóa phần dự - ở đầu
      str = str.replace(/^-+/g, '');
   
      // xóa phần dư - ở cuối
      str = str.replace(/-+$/g, '');
   
      // return
      return str;
  }
  render() {
    var items1 = [];
    var items2 = [];
    var items3 = [];
    var items4 = [];
    var items5 = [];
    data.map((value, key) => {
      if(this.chuyenDoiURL(value.theme) === this.chuyenDoiURL("Assassin")){
        var item1 = {};
            item1.id = value.id;
            item1.view = value.view;
            item1.theme = value.theme;
            item1.title = value.title;
            item1.quote = value.quote;
            item1.content = value.content;
            item1.image = value.image;
        
        items1.push(item1);
        return items1;
      }
      else if(this.chuyenDoiURL(value.theme) === this.chuyenDoiURL("Archer")){
        var item2 = {};
            item2.id = value.id;
            item2.view = value.view;
            item2.theme = value.theme;
            item2.title = value.title;
            item2.quote = value.quote;
            item2.content = value.content;
            item2.image = value.image;
        items2.push(item2);
        return items2;
      }
      else if(this.chuyenDoiURL(value.theme) === this.chuyenDoiURL("Magician")){
        var item3 = {};
            item3.id = value.id;
            item3.view = value.view;
            item3.theme = value.theme;
            item3.title = value.title;
            item3.quote = value.quote;
            item3.content = value.content;
            item3.image = value.image;
        
        items3.push(item3);
        return items3;
      }
      else if(this.chuyenDoiURL(value.theme) === this.chuyenDoiURL("Fighter")){
        var item4 = {};
            item4.id = value.id;
            item4.view = value.view;
            item4.theme = value.theme;
            item4.title = value.title;
            item4.quote = value.quote;
            item4.content = value.content;
            item4.image = value.image;
        items4.push(item4);
        return items4;
      }
      else if(this.chuyenDoiURL(value.theme) === this.chuyenDoiURL("Support")){
        var item5 = {};
            item5.id = value.id;
            item5.view = value.view;
            item5.theme = value.theme;
            item5.title = value.title;
            item5.quote = value.quote;
            item5.content = value.content;
            item5.image = value.image;
        items5.push(item5);
        return items5;
      }
      else{return true;}
      
    });
    
    return (
      
      <div className="list_menu get_data_menu" data-get-menu="listmenu">
        {/* start slider section */}
        <section>
          <div className="slide_listmenu swiper-container">
            <div className="swiper-wrapper">
              {/* start slider item */}
              <div className="swiper-slide start_item_listmenu">
              </div>
              {/* end slider item */}
              {/* start slider item */}
              <ContentListMenu title="Assassin" dataTheme={items1}/>
              {/* end slider item */}
              {/* start slider item */}
              <ContentListMenu title="Archer" dataTheme={items2}/>
              {/* end slider item */}
              {/* start slider item */}
              <ContentListMenu title="Magician" dataTheme={items3}/>
              {/* end slider item */}
              {/* start slider item */}
              <ContentListMenu title="Fighter" dataTheme={items4}/>
              {/* end slider item */}
              {/* start slider item */}
              <ContentListMenu title="Support" dataTheme={items5}/>
              {/* end slider item */}
              {/* start slider item */}
              <div className="swiper-slide end_item_listmenu">
              </div>
            </div>
            {/* start slider swiper pagination */}
            <div className="swiper-scrollbar xs-display-none" />
            <div className="swiper-pagination-vertical position-fixed bullet-deep-pink z-index-5" />
            {/* end slider swiper pagination */}
          </div>
        </section>
      </div>

    )
  }
}
